import Fuse from "fuse.js";
import { Adapter } from "@/adapters/Adapter";
export class FuseAdapter extends Adapter {
    constructor() {
        super(...arguments);
        this.name = "Fuse.js";
    }
    get defaultOptions() {
        return {
            includeMatches: true,
            includeScore: true,
            shouldSort: true
        };
    }
    initScorer() {
        return this.scorer = new Fuse(this.items, this.options);
    }
    convertResults(results) {
        return results.map(item => {
            const { score = 0, matches = [] } = item;
            const matchesHash = {};
            const matchesByKey = {};
            const scores = {};
            const qsScore = 1 - score;
            const [bestMatch] = matches;
            const scoreKey = (bestMatch && bestMatch.key) || "";
            for (const match of matches) {
                // key is marked optional on FuseResult, so make TS happy
                if (match.key) {
                    // add 1 to the end of the range so it can be passed
                    // to substring()
                    matchesByKey[match.key] =
                        match.indices.map(([start, end]) => [start, end + 1]);
                }
            }
            for (const key of this.keys) {
                scores[key] = key === scoreKey ? qsScore : 0;
                matchesHash[key] = matchesByKey[key] || [];
            }
            return {
                item: item.item,
                score: qsScore,
                scoreKey,
                // cast scoreKey to keyof T so TS knows it's a key on the
                // original item
                scoreValue: String(item.item[scoreKey]),
                scores,
                matches: matchesHash
            };
        });
    }
}
